
.welcome {
    position: fixed;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 6;
    background: #000b17;
    -webkit-animation: welcome 0.5s;
            animation: welcome 0.5s;
    transition: 0.3s;
    -webkit-transition: 0.3s;
}
.welcome img {
    width: 100%;
    height: 100%;
}
.welcome.hide{
    opacity: 0;
    visibility: hidden;
}




